@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.contact-card {
  height: 550px;
  width: 400px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  border-radius: 6px;
  box-shadow: rgb(37, 47, 67) 0px 8px 30px -10px;
  margin-top: 140px;
  transition: all ease 300ms;
}

.contact-card:hover {
  transform: scale(1.02, 1.02);
  box-shadow: rgb(37, 47, 67) 0px 8px 30px -12px;
}

.card-details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}

.github, .insta, .linkedin {
  background-color: black;
  width: 70%;
  height: 44px;
  margin: 12px auto;
  display: flex;
  padding-left: 40px;
  align-items: center;
  border-radius: 6px;
  transition: all ease 300ms;
}

.github:hover, .insta:hover, .linkedin:hover {
  transform: scale(1.02, 1.02);
}

.github > a,
.github > a:hover,
.insta > a, 
.insta > a:hover, 
.linkedin > a,
.linkedin > a:hover {
  color: white;
}

.fa-icon {
  color: white;
  font-size: 20px;
  padding-right: 10px;
}

.profile {
  width: 100%;
}

@media only screen and (max-width: 425px) {
  .contact-card {
    height: 525px;
    width: 350px;
    margin-top: 140px;
  }
}

@media only screen and (max-width: 375px) {
  .contact-card {
    height: 488px;
    width: 325px;
    margin-top: 130px;
  }
}

@media only screen and (max-width: 320px) {
  .contact-card {
    height: 420px;
    width: 280px;
    margin-top: 110px;
  }
  .insta, .linkedin {
    height: 40px;
  }
  .insta > a, .linkedin >a {
    color: white;
    font-size: 0.9rem !important;
  }
}
.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.about {
  max-width: 700px;
  margin: 20px 40px;
  font-family: "Oswald", sans-serif;
}

p {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1.1rem;
  font-weight: 300;
}

@media only screen and (max-width: 375px) {
  p {
    font-size: 0.95rem;
  }
}
a {
  color: #282c34;
  cursor: pointer;
  font-weight: 300;
  text-decoration: none;
  transition: color ease 350ms;
  padding: 6px 0;
}

a:hover {
  color: #ec5200;
}

.nav-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  background-color: white;
  position: fixed;
  top: 0;
  box-shadow: rgb(37, 47, 67) 0px 10px 10px -12px;
  z-index: 9999;
}

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 40px;
  height: 80px;
  width: 700px;
}

.brand {
  color: #282c34;
  font-family: "Oswald", sans-serif;
  font-size: 1.6rem;
  font-weight: 900;
  text-decoration: none;
  text-transform: uppercase;
}

.brand:hover {
  color: #282c34;
}
.container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

